<script>
import Header from "@/components/Header2/header2.vue";
import Footer from "@/components/Footer/footer.vue";
export default {
  name: "companyApplyAccount-component",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      active: 0,
      footerHeight: 0,
      fixedBtnGroupHeight: 0,
    };
  },
  methods: {},
  mounted() {
    this.$root.$children[0].sidebarSwitch = false;
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);

    const footer = document.querySelector(".footer");
    const fixedBtnGroup = document.querySelector(".fixed-btn-group");
    if (footer && fixedBtnGroup) {
      this.footerHeight = footer.offsetHeight;
      this.fixedBtnGroupHeight = fixedBtnGroup.offsetHeight;
    }

    // console.log(this.footerHeight, this.fixedBtnGroupHeight);
  },
};
</script>

<template src="./companyApplyAccountStep1.html"></template>
